/* GENERAL */
/*-------------------------------------------------------------------------------------------------------------------*/
body {
    color: $colorprimario;
    font-family: $fontprincipal;
  }
.navbar {
height: 80px;
}
.navbar-toggler-icon {
 background-color: $colorprimario;
}
.navbar-toggler {
  color: $colorprimario;
  border-color: $colorprimario;
  margin-left: 1rem;
}
#navContainer  {
  background-color: rgba(0, 0, 0, .7);
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  min-height: 100%;
}
.navbar li a {
    color: $colorprimario;
    font-size: 19px;
    font-weight: 600;
    letter-spacing: 2.7px;
    line-height: 23px;
}
#home-section {
    background: url(../../dist/img/background.jpg) no-repeat;
    max-width: 100%;
    height: 1200px;
    min-height: 100vh;
    background-size: cover;
    background-attachment: fixed; 

  }
.dark-overlay {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    min-height: 100%;
  }
  .header-container {
    padding-top: 400px;
  }
  .main-col {
    border: solid 4px;
    border-color: $colorgold;
    background-color: rgba(0, 0, 0, .4);
  }
  .main-img-logo {
    height: 113px;
    width: 191.41px;
    position: absolute;
    top: 37%;
    left: 44.5%;
  }
  .main-img-text-logo {
    margin: 1rem;
    padding:1rem 2rem;
  }
   .mainh2 {
    color: $colorprimario;
    font-family: $fontprincipal;
    font-size: 25px;
    font-weight: 600;
    letter-spacing: 2.25px;
    line-height: 29px;
    text-transform: uppercase;
    padding:1rem 2rem;
  }
  .mainh3 {
    color: $colorprimario;
    font-family: $fontprincipal;
    font-size: 22px;
    font-style: italic;
    letter-spacing: 4.13px;
    line-height: 14px;
  }
  .mainp {
    color: $colorprimario;
    font-family: $fontprincipal;
    font-size: 20px;
    letter-spacing: 0;
    line-height: 30px;
    text-align: center;
    padding-bottom: 5rem;
  }
.main-location {
box-sizing: border-box;
  height: 60px;
  width: 200px;
  border: 4px solid #AB965D;
  background-color: #000000;
  position: absolute;
  top: 95%;
  left: 44%;
}
.main-locationp {
  color: $colorprimario;
  padding: auto;
  margin: auto;
}
p, h1 {
  color: $colorsecundario;
}
.section-compromise  {
  background-color: #F6F4EE;
  padding-top: 3rem;
  padding-bottom: 6rem;
}
.compromisep {
  color: $colorgold;
  font-weight: bold;
  font-size: 20px;
  text-transform: uppercase;
}
.compromisep2 {
  color: $colorsecundario;
  font-family: $fontprincipal;
  font-size: 16px;
  text-transform: uppercase;
  font-style: italic;
  letter-spacing: 3px;
}
.compromiseh1 {
  color: #000000;
  font-family: "Minion Pro";
  font-size: 28px;
  letter-spacing: 11px;
  line-height: 50px;
  text-align: center;
  text-transform: uppercase;
}
.compromisep3 {
  color: #000000;
  font-family: "Minion Pro";
  font-size: 25px;
  font-weight: 600;
  letter-spacing: 1.94px;
  line-height: 32px;
  text-align: center;
  text-transform: uppercase;
}
.compromisep4 {
  color: #000000;
  font-family: "Minion Pro";
  font-size: 18px;
  letter-spacing: 0;
  line-height: 37px;
  text-align: center;
}
.compromise-img {
  width: 100%;
  height: 100%;
}
.compromisech {
  color: #000000;
  font-family: Copperplate;
  font-size: 24px;
  letter-spacing: 2.48px;
  line-height: 24px;
}
.compromiseny {
  opacity: 0.35;
  color: #000000;
  font-family: Copperplate;
  font-size: 24px;
  letter-spacing: 2.48px;
  line-height: 24px;
}
footer {
  background-color: #221F1D;
}
.logoboarshead {
  height: 100%;
  width: 80%;
}
.familyp {
  color: #A7A49E;
  font-family: "Minion Pro";
  font-size: 29px;
  font-style: italic;
  letter-spacing: 0;
  line-height: 49px;
  text-align: right;
  
}
.footerp {
  color:$colorgold;
  font-family: "Minion Pro";
  font-size: 26px;
  font-weight: 600;
  letter-spacing: 1.77px;
  line-height: 31px;
  margin-top: 2.5rem;
  margin-right: 2rem;

}
.footer-row1 {
  box-sizing: border-box;
  border-bottom: 1px solid #837A70;
  border-right: 1px solid #837A70;
  border-top: 1px solid #837A70;
}
.footer-row2 {
  box-sizing: border-box;
  border-bottom: 1px solid #837A70;
  border-left: 1px solid #837A70;
  border-top: 1px solid #837A70;
}
.footerep {
  color: #A7A49E;
  font-family: Copperplate;
  font-size: 23px;
  letter-spacing: 1.08px;
  line-height: 24px;
  text-align: center;
}
.footerep2 {
  color: #FFFFFF;
  font-family: "Minion Pro";
  font-size: 19px;
  letter-spacing: 0;
  line-height: 26px;
}
.footerpicon {
 
  text-align: center;
}
.footer-img-icon {
 width: 20%;
 height: 20%;
 margin: auto;
 padding: auto;
}
.footer-img-icon2 {
  width: 20%;
